___CSS_LOADER_EXPORT___.push([module.id, `body {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

header,
footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

dialog {
  border-radius: 10px;
}

dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(12px);
}

.dialog-button-container {
  display: flex;
  justify-content: center;
  gap: 5%;
}

.mode-button {
  font-size: 24px;
  border-radius: 5px;
}

#single-input-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

#dual-input-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

#title {
  font-size: 100px;
  text-align: center;
}

#gameboard-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

#ui-container {
  height: 100%;
  width: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

#message-container {
  text-align: center;
  font-size: 24px;
}

.gameboard {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 1px;
}

.coordinate {
  height: 50px;
  width: 50px;
  border: solid black 2px;
  display: grid;
  place-items: center;
  font-size: 30px;
  border-radius: 5px;
}

.ship {
  background-color: rgb(113, 197, 113);
}

.hit {
  background-color: rgb(255, 129, 129);
}

.miss {
  background-color: rgb(133, 133, 255);
}
